import secFooter from "@/components/SecFooter/SecFooter.vue"
import uniNumberBox from '@/components/uni-number-box/uni-number-box.vue'
import empty from "@/components/mycom/empty";
export default {
	components: {
		uniNumberBox,
		secFooter,
		empty
	},
	data() {
		return {
			checked: false,
			topText: '完成',
			bianji: true,
			tarbaA: 3,
			total: 0, //总价格
			allChecked: false, //全选状态  true|false
			empty: false, //空白页现实  true|false
			dataList: [],
			uid: ""
		};
	},
	onShow() {
		if (uni.getStorageSync('uid')) {
			this.uid = uni.getStorageSync('uid'),
				getApp().globalData.uid = this.uid
		} else {
			this.uid = ''
		}
		console.log(this.uid)
		if (this.uid == '') {
			this.empty = true
		} else {
			this.initData();
		}

	},
	watch: {
		//显示空白页
		dataList(e) {
			let empty = e.length === 0 ? true : false;
			if (this.empty !== empty) {
				this.empty = empty;
			}
		}
	},
	onHide() {
		this.dataList = [];
	},
	computed: {},
	methods: {
		navToLogin() {
			if (this.uid == '') {
				this.$api.navTo('/pagesB/author/index')
			} else {
				uni.redirectTo({
					url: '/pages/tabbar/home'
				});
			}

			console.log(1)
		},
		initData() {

			this.loadData();
		},
		loadData() {
			let parmas = {
				uid: this.uid,
			};
			this._reqw.ipost(parmas, "cartList").then(res => {
				res.result == 0 ? (res.dataList != undefined && res.dataList.length != 0 ? res.dataList.forEach(item => {
					item.checked = false;
					this.dataList.push(item)
				}) : this.empty = true) : this.$api.tip(res.resultNote)
			}).catch(err => {})

		},
		//选中状态处理
		check(type, index) {
			if (type === 'item') {
				this.dataList[index].checked = !this.dataList[index].checked;
			} else {
				const checked = !this.allChecked
				const list = this.dataList;
				list.forEach(item => {
					item.checked = checked;
				})
				this.allChecked = checked;
			}
			this.calcTotal(type);
		},
		//数量
		numberChange(data) {
			console.log(data)
			this.dataList[data.index].count = data.number;
			this.calcTotal();

		},
		//  编辑
		changDel() {
			this.topText == '完成' ? this.topText = "编辑" : this.topText = "完成";
			this.bianji = !this.bianji;
		},
		//删除
		deleteCartItem(list) {
			let _this = this;
			uni.showModal({
				title: "确定删除吗",
				cancelText: "取消",
				confirmText: "确定",
				confirmColor: "#00736B",
				titleColor: "#00736B",
				success(res) {
					if (res.confirm) {
						let cartId = []
						console.log(list, cartId)
						list.id != undefined ? cartId.push(list.id) : list.forEach(item => {
							cartId.push(item.id)
						})
						// typeof list != Array ? cartId.push(list) :cartId ;

						let parmas = {
							uid: _this.uid,
							cartIds: cartId.join(',')
						}
						_this._reqw.ipost(parmas, "delCart").then(res => {
							res.result == 0 ? (_this.$api.tip('删除成功!'), _this.initData(), _this.calcTotal()) : _this.$api.tip(res.resultNote)
						})
					} else if (res.cancel) {
						console.log("取消")
					}
				}
			})

		},
		//计算总价
		calcTotal() {
			let list = this.dataList;
			if (list.length === 0) {
				this.empty = true;
				return;
			}
			let total = 0;
			let checked = true;
			list.forEach(item => {
				if (item.checked === true) {
					total += item.price * item.count;
				} else if (checked === true) {
					checked = false;
				}
			})
			this.allChecked = checked;
			this.total = this.$api.parsePrice(total);
		},
		//创建订单
		createOrder(n) {
			let list = this.dataList.filter(item => {
				return item.checked
			});
			list.length > 0 ? (n == 1 ? this.deleteCartItem(list) : (uni.setStorageSync('good', JSON.stringify(list)),
					setTimeout(() => {
						this.$api.navTo(
							`/pagesD/order/finishOrder?payType=1&type=0`)
					}, 200))) :
				this
				.$api.tip('请选择商品!')
		}
	}
}
